import { Component } from '@angular/core';
import { IonicPage, NavController, App, NavParams, AlertController  } from 'ionic-angular';
import { HttpProvider } from '../../providers/http/http';
import { UtilsProvider } from '../../providers/utils/utils';
import { Storage } from '@ionic/storage';
import { CommandsProvider } from '../../providers/commands/commands';

/**
 * Generated class for the ProductToPayPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-product-to-pay',
  templateUrl: 'product-to-pay.html',
})
export class ProductToPayPage {

  public message: string; 
  public lista1 : string;
  public strHead : string;
  public option : string;
  public posicion: number;
  public cardNumber : string;
  public creditLimit : string;
  public nextStatement : string;
  public lastPayment : string;
  public balance : string;
  public available : string;
  public minimumPayment : string;
  public date : string;

  public numberCard: string;
  public accountToDebit: string;
  public accountToDebitNumber: string;
  //public bandera: any;
  public radioButton: string;
  private canExit : boolean;
  public valorSelected: string;
  private CREDIT_CARD:string ='creditCard';
  public nameCreditCard: string;
  public numberCreditCard: string;
  public head: string;

  public selectedAccount: string;
  public minimo: string;
  public total: string;
  public creditLst: string;

  constructor(public navCtrl: NavController, public app: App, public navParams: NavParams, public http: HttpProvider, private storage: Storage, public utils: UtilsProvider,
              private alertCtrl: AlertController, public command: CommandsProvider) {
    
    //Obtenci贸n del numero de la tarjeta en la variable de sesion
    /* this.storage.get('numero').then((val) => {
      this.numberCard = val;  
      //alert("numero->"+this.numberCard);    
    });*/

    //Obtencion del nombre de la tarjeta de p-credit 
    this.storage.get('name_CreditCard').then((val) => {
      if (val != null) {
        this.nameCreditCard = val;
      }
    });

    this.storage.get('number_CreditCard').then((val) => {
      if (val != null) {
        this.numberCreditCard = val;
      }
    }); 

    //Obtencion de valor minimo y total de la tarjeta de credito -> se obtiene de ProductOverView
      this.storage.get('creditLst').then((val) => {
        this.selectedAccount = val[0];
        this.minimo = this.selectedAccount[5];
        this.total = this.selectedAccount[3];
       
      });

  }

  ionViewCanEnter() { 
     //para validar se se necesita OTP
     return true;
  }

  //2
  ionViewDidLoad() {

    //obtenci贸n de la lista guardada en sesi贸n para no volver hacer la consulta
    this.storage.get('head').then((val) =>    { if(val != null ){ this.head = val; } });

    this.storage.get('accountSelectedTopay').then((val) => {
      if(val != null){
        //this.accountToDebit = val[1];
        this.accountToDebit = val;
      }
     });

     //obtencion de la variable de sesion de amount to pay
     this.storage.get('valorSeleccionado').then((val) => {
      if(val != null){
        this.valorSelected = val;
      }
     });

  }

  //4
  ionViewDidEnter(){   
    if(this.head == null || this.head == undefined){
      //alert("primero "+ (this.head == null) + "\n segundo "+ (this.head == undefined) );
      this.loadProductToPay(); 
      this.storage.set('selectedAccount', this.strHead);
    }
    this.loadCreditCard();
    
   // this.loadProductToPay();     
  }
/**
 *  Before close page, remove storege elements and avoid load garbage 
 *  paginas y eventos destruidos
 */
  ionViewWillUnLoad(){
    this.cleanDataStore();
  }

  private loadProductToPay() {   

    var command :string[] =  this.utils.getCommands(this.command.E2LSCR89L);
    //var command = 'E2LSCR89L'; 
    
    var param = command[0] + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+'&E2EW=' + this.utils.separator + '' + this.utils.separator + this.utils.separator + 'E2LSCR72' + this.utils.separator + '0' + this.utils.separator + this.utils.separator + '&PAR=1+%BA'+ this.numberCreditCard + '+%BA12162017+%BA01162018+%BA1+%BA1+%BAD+%BA';

    this.http.callServer(param).then(res => {      
    this.message = res.message;

      if (this.message != '' && typeof this.message!=="undefined") {
        this.utils.alertServidor(this.message);
      } else {
        this.strHead = res.strHead;
        this.option = res.optional;        
        this.cardNumber = this.strHead[0];
        this.creditLimit = this.strHead[1];
        this.nextStatement = this.strHead[3];
        this.lastPayment = this.strHead[4];
        this.balance = this.strHead[16];
        this.available = this.strHead[17];
        this.minimumPayment  = this.strHead[2];
        this.date = this.strHead[5];
        //alert(this.strHead);
        this.storage.set('head', this.strHead );
        
        this.storage.set('selectedAccount', this.strHead);
      }
    },
      error => {
        console.log(error);
      });
  }

  //Metodo para ir a la pagina product to debit
  productToDebit(){
    //this.navCtrl.push("ProductToDebitPage",{flag: this.CREDIT_CARD});
    
    this.navCtrl.push("ProductToDebitCCardPage",{flag: this.CREDIT_CARD});
  }

  //Metodo para ir a la pagina amount to pay
  amountToPay(){
    this.navCtrl.push("AmountToPayPage");
  }

  private cleanDataStore(){
    this.accountToDebit = null;
    this.accountToDebitNumber ="";
    this.storage.remove("accountSelectedTopay");
    this.storage.set("restart","clean");
  }

  cancel(){
    //this.utils.cleanAll();
   // this.storage.remove('numero');
   // this.storage.remove('accountSelectedTopay');
   // this.storage.remove('valorSeleccionado');
   this.storage.remove("accountSelectedTopay");
    this.cleanDataStore();
    this.navCtrl.push("RutaPagosPage");
  }

  
  //Metodo para realizar el pago
  pay(){
    //alert((this.cardNumber != null) +" - "+ (this.valorSelected != null) +" - "+ (this.accountToDebit != null));
    //const valorSeleccionado = parseInt(this.valorSelected);
    const valorSeleccionado = this.valorSelected;

    if(this.numberCreditCard != null && this.valorSelected != null && this.accountToDebit != null){
      if(valorSeleccionado != "0"){
        this.navCtrl.push("ConfirmPage",{ "cardNumber": this.numberCreditCard,  //this.cardNumber,
                                       //"valorSelected": this.valorSelected,
                                       "valorSelected": valorSeleccionado,
                                       "accountToDebit": this.accountToDebit,
                                       "flag": this.CREDIT_CARD });
      }
      else{
        this.utils.alertCampos("productToPay.mensaje");
      }
      
    }
    else{
      //this.utils.alertCampos("productToPay.alert");
      this.validarCamposVacios();
    }
  }


   //Metodo para cargar antes del pago de credit Card
   private loadCreditCard(){
    
    //var cmd = "E2ADMR67PT";
    var command :string[] =  this.utils.getCommands(this.command.E2ADMR67PT);
    //alert("Tipo de pago: "+tipoPago+"\n cuenta a descontar: "+ account+"\n Monto: "+ monto);    
    var URL=  command[0]+ '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+ '&E2EW='+
    this.utils.separator+"PAGAR" +this.utils.separator+'0'+ this.utils.separator+'E2LSCR89L' +this.utils.separator+'0'+this.utils.separator+
    this.utils.separator + '&PAR=' + this.nameCreditCard +this.utils.separator+ this.numberCreditCard + this.utils.separator+ this.minimo + 
    this.utils.separator + this.total;

    this.http.callServer(URL).then(res => {      
      this.message = res.message;
  
        if (this.message != '' && typeof this.message!=="undefined") {
          this.utils.alertCampos(this.message);
        } else {
          //this.comprobanteTransfer = res.lista1;
          
         // this.navCtrl.push("VoucherTransferPage", {comprobanteTransfer: this.comprobanteTransfer});
        }
      },
        error => {
          console.log(error);
        });
  }

  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }

  //Metodo de validaci贸n de campos - SOLO EN PARA MOSTRAR LOS ALERT Y LAS RAYAS ROJAS
  public validarCamposVacios(){
    
    if(this.valorSelected == null && this.accountToDebit == null){
        
      this.utils.presentAlert('productToPay.validateCampos');
      this.colorAmountToPay();
      this.colorAmountToDebit();
    }
    else if(this.valorSelected == null){
      this.colorAmountToPay();
      this.utils.presentAlert('productToPay.validateAmount');
    }

    else if (this.accountToDebit == null) {
      this.colorAmountToDebit();
      this.utils.presentAlert('productToPay.validateAccount');
    }
  }

  //se traza la raya roja del account from
  public colorAmountToDebit(){
    document.getElementById("colorAmountToDebit").style.borderBottom = "solid 1px red";
  }

  //se traza la raya roja del amount
  public colorAmountToPay(){
    document.getElementById("valorSelectedAmountToPay").style.borderBottom = "solid 1px red";
  }

  
}



